/* Hidden horizontal scroll bar */
body
{
	overflow-x: hidden;
}

/* Footer Properties */
.mastfoot
{
	position: fixed;
	width: 100%;
	bottom:0px;
	padding: 5px;
	background-color: #F8F8F8;
	border-style: solid;
	border-width: 1px;
	border-color: #E7E7E7;
}

/* Centered Columns */
.row-centered 
{
	text-align:center;
}
.col-centered 
{
	float: none;
	margin: 0 auto;
}

/* Override colour for player info panels */
input[readonly].player-info
{
	background-color: #fff;
}

/* Style for selected player */
.player-focused
{
	border-color: rgba(126, 239, 104, 0.8);
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgba(126, 239, 104, 0.6);
	outline: 0 none;
}